import React, { useState, useEffect } from "react"
import Toggle from 'react-toggle';
import "react-toggle/style.css"
import sun from '../assets/svg/open.svg';
import moon from '../assets/svg/close.svg';
import styled from 'styled-components';
const ToggleWrapper = styled.div`
    display: flex; 
    justify-content: space-between;
    align-items: center;
    width: 10rem;
`;
const ModeToggle = (props) => {
    console.log(props);
    // if (typeof window !== 'undefined') {
    // 在浏览器环境下执行相关代码
    // }
    const [theme, setTheme] = useState();
    //不需要跟踪theme依赖
    useEffect(() => {
        setTheme(sessionStorage.getItem('THEME') ?? 'light')
        document.body.className = theme
    }, [theme])
    const setBodyTheme = (name) => {
        setTheme(name)
        // if (typeof window !== 'undefined') {
        // 在浏览器环境下执行相关代码
        sessionStorage.setItem('THEME', name)
        // }
        document.body.className = name
    }
    return (
        <ToggleWrapper >
            <Toggle
                icons={{
                    checked: (
                        <img
                            src={moon}
                            width="50"
                            height="50"
                            alt="关"
                            style={{ pointerEvents: 'none' }}
                        />
                    ),
                    unchecked: (
                        <img
                            src={sun}
                            width="50"
                            height="50"
                            alt="开"
                            style={{ pointerEvents: 'none' }}
                        />
                    )
                }}
                checked={theme === 'dark'}
                onChange={e =>
                    setBodyTheme(e.target.checked ? 'dark' : 'light')
                }
                title="切换主题"
            />
            <div>
                暗黑模式
            </div>
        </ToggleWrapper>

    )
}

export default ModeToggle